/* ===================================== */
/* ===== ROOT COLORS ===== */
/* ===================================== */

:root {
  --deep-green: #0e1d19;
  --primary-dark: #162622;
  --accent-gold: #C6A75E;
  --light-bg: #f5f4f1;
}

/* ===================================== */
/* ===== GLOBAL ===== */
/* ===================================== */

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--light-bg);
  font-family: 'Poppins', sans-serif;
  color: #222;
}

/* ===================================== */
/* ===== TRANSPARENT ROYAL NAVBAR ===== */
/* ===================================== */

.site-navbar-wrap {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 28px 0;
  z-index: 1000;
  transition: 0.35s ease;
}

.site-navbar {
  background: transparent;
  box-shadow: none;
}

/* Navbar text control */

.site-logo a,
.site-menu button {
  color: #ffffff;
}

.site-navbar-wrap.scrolled .site-logo a,
.site-navbar-wrap.scrolled .site-menu button {
  color: #111;
}

/* Logo */

.site-logo a {
  font-family: 'Playfair Display', serif;
  font-size: 2.1rem;
  font-weight: 600;
  letter-spacing: 1px;
  text-decoration: none;
}

/* Menu */

.site-menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.site-menu li {
  display: inline-block;
  margin-left: 50px;
}

.site-menu button {
  background: none;
  border: none;
  font-family: 'Poppins', sans-serif;
  font-size: 13px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s ease;
  position: relative;
}

/* Gold underline */

.site-menu button::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -8px;
  width: 0%;
  height: 1.5px;
  background: var(--accent-gold);
  transition: width 0.3s ease;
}

.site-menu button:hover::after,
.site-menu li.active button::after {
  width: 100%;
}

.site-menu button:hover {
  color: var(--accent-gold);
}

/* ===================================== */
/* ===== HERO SECTION ===== */
/* ===================================== */

.site-blocks-cover {
  position: relative;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 140px;
}

.site-blocks-cover::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0,0,0,0.45);
  z-index: 1;
}

.hero-overlay {
  position: relative;
  z-index: 2;
  text-align: center;
  padding: 0 20px;
}

.hero-content {
  color: #ffffff;
  max-width: 900px;
  margin: auto;
}

.hero-content h1 {
  font-family: 'Playfair Display', serif;
  font-size: 76px;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 20px;
  text-shadow: 0 15px 45px rgba(0,0,0,0.6);
}

.hero-content h1::after {
  content: "";
  display: block;
  width: 90px;
  height: 2px;
  background: var(--accent-gold);
  margin: 25px auto 0;
}

.hero-content .caption {
  font-size: 18px;
  opacity: 0.9;
  margin-top: 30px;
  margin-bottom: 45px;
}

.hero-btn {
  background: var(--accent-gold);
  color: #000;
  padding: 15px 45px;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: 0.3s ease;
}

.hero-btn:hover {
  background: #b8921e;
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0,0,0,0.3);
}

/* ===================================== */
/* ===== SECTION SPACING ===== */
/* ===================================== */

.site-section {
  padding: 140px 0;
}

/* ===================================== */
/* ===== ROOM CARDS ===== */
/* ===================================== */

.hotel-room {
  border-radius: 18px;
  overflow: hidden;
  transition: 0.4s ease;
}

.hotel-room:hover {
  transform: translateY(-8px);
  box-shadow: 0 25px 60px rgba(0,0,0,0.25);
}

.hotel-room img {
  transition: 0.4s ease;
}

.hotel-room:hover img {
  transform: scale(1.05);
}

/* ===================================== */
/* ===== LUXURY REVIEWS ===== */
/* ===================================== */

.reviews-section {
  padding: 140px 0;
  background-color: #f8f6f2;
  text-align: center;
}

.reviews-title {
  font-family: 'Playfair Display', serif;
  font-size: 42px;
  margin-bottom: 80px;
  color: var(--primary-dark);
}

.reviews-title::after {
  content: "";
  display: block;
  width: 80px;
  height: 2px;
  background: var(--accent-gold);
  margin: 20px auto 0;
}

.reviews-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 50px;
  max-width: 1200px;
  margin: auto;
}

.review-card {
  background: #fff;
  padding: 50px 40px;
  border-radius: 18px;
  border: 1px solid rgba(0,0,0,0.05);
  transition: 0.4s ease;
}

.review-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 25px 60px rgba(0,0,0,0.08);
}

.stars {
  color: var(--accent-gold);
  font-size: 18px;
  margin-bottom: 25px;
}

.review-text {
  font-size: 16px;
  line-height: 1.8;
  color: #555;
  margin-bottom: 30px;
  font-style: italic;
}

.review-name {
  font-weight: 600;
  color: var(--primary-dark);
  letter-spacing: 1px;
}

/* ===================================== */
/* ===== PREMIUM CONTACT SECTION ===== */
/* ===================================== */

.contact-section {
  padding: 140px 0;
  background: #f7f5f1;
}

.contact-card,
.contact-info-box {
  background: #ffffff;
  padding: 60px 50px;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0,0,0,0.08);
}

/* Address alignment */

.address-row {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 25px;
}

.address-row i {
  color: var(--accent-gold);
  font-size: 18px;
}

.address-row span {
  font-size: 15px;
  line-height: 1.6;
  color: #968d08;
}

/* Form styling */

.contact-card input,
.contact-card select {
  width: 100%;
  padding: 14px 18px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 14px;
  transition: 0.3s ease;
}

.contact-card input:focus,
.contact-card select:focus {
  outline: none;
  border-color: var(--accent-gold);
  box-shadow: 0 0 0 2px rgba(198,167,94,0.2);
}

.contact-card button {
  background: var(--accent-gold);
  color: #000;
  padding: 14px 35px;
  border-radius: 40px;
  border: none;
  font-weight: 600;
  transition: 0.3s ease;
}

.contact-card button:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0,0,0,0.15);
}

/* ===================================== */
/* ===== FOOTER ===== */
/* ===================================== */

.site-footer {
  background: var(--deep-green);
  color: white;
  padding: 100px 0;
}

.site-footer a {
  color: var(--accent-gold);
  transition: 0.3s ease;
}

.site-footer a:hover {
  color: white;
}

/* ===================================== */
/* ===== WHATSAPP FLOAT ===== */
/* ===================================== */

.whatsapp-float {
  position: fixed;
  width: 64px;
  height: 64px;
  bottom: 35px;
  right: 35px;
  background-color: #25D366;
  color: #fff;
  border-radius: 50%;
  font-size: 30px;
  box-shadow: 0 25px 60px rgba(0,0,0,0.45);
  z-index: 99999;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease;
}

.whatsapp-float:hover {
  transform: scale(1.1);
}
/* ===== CONTACT ICON SPACING FIX (OVERRIDE ONLY) ===== */

.address-flex {
  display: flex;
  align-items: center;
  gap: 18px !important;      /* Force spacing */
  margin-top: 8px;
}

.address-flex i {
  color: var(--accent-gold);
  font-size: 20px;
  flex-shrink: 0;
}

.address-flex span {
  font-size: 16px;
  color: #333;
  line-height: 1.6;
}
/* ===================================== */
/* ===== PREMIUM CONTACT CARD UPGRADE ===== */
/* ===================================== */

.contact-info-card {
  background: #ffffff;
  padding: 55px 45px;
  border-radius: 22px;
  box-shadow: 0 25px 70px rgba(0,0,0,0.08);
}

.contact-title {
  font-family: 'Playfair Display', serif;
  font-size: 30px;
  margin-bottom: 45px;
  color: var(--primary-dark);
}

.contact-item {
  margin-bottom: 35px;
}

.contact-label {
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #888;
  margin-bottom: 12px;
}

/* Clickable rows */

.contact-row {
  display: flex;
  align-items: center;
  gap: 18px;
  text-decoration: none;
  padding: 12px 14px;
  border-radius: 12px;
  transition: all 0.3s ease;
  color: #333;
}

.contact-row i {
  color: var(--accent-gold);
  font-size: 18px;
  min-width: 20px;
}

/* Hover effect for luxury feel */

.contact-row:hover {
  background: rgba(198, 167, 94, 0.08);
  transform: translateX(4px);
  color: var(--primary-dark);
}
/* ===== REFINED CONTACT CARD ===== */

.contact-info-card {
  background: #ffffff;
  padding: 40px 35px;        /* Reduced padding */
  border-radius: 18px;
  box-shadow: 0 20px 60px rgba(0,0,0,0.06);
}

.contact-title {
  font-family: 'Playfair Display', serif;
  font-size: 28px;
  margin-bottom: 30px;       /* Reduced */
  color: var(--primary-dark);
}

.contact-item {
  margin-bottom: 22px;       /* Reduced spacing */
}

.contact-label {
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  color: #888;
  margin-bottom: 6px;        /* Reduced */
}

.contact-row {
  display: flex;
  align-items: center;
  gap: 14px;                 /* Slightly tighter */
  text-decoration: none;
  padding: 6px 0;            /* Less vertical padding */
  border-radius: 8px;
  transition: all 0.25s ease;
  color: #333;
}

.contact-row i {
  color: var(--accent-gold);
  font-size: 17px;
  min-width: 18px;
}

.contact-row:hover {
  color: var(--primary-dark);
  transform: translateX(3px);
}
/* ================================= */
/* ===== PREMIUM MOBILE VERSION ==== */
/* ================================= */

@media (max-width: 768px) {

  /* ===== NAVBAR ===== */

  .site-navbar-wrap {
    padding: 10px 18px;
    backdrop-filter: blur(12px);
  }

  .site-logo {
    font-size: 18px !important;
    letter-spacing: 1px;
  }

  .site-menu-toggle {
    font-size: 26px;
    color: #fff;
  }

  /* ===== HERO SECTION ===== */

  .hero-title,
  h1 {
    font-size: 34px !important;
    line-height: 1.2;
    text-align: center;
  }

  .hero-subtitle {
    font-size: 13px !important;
    letter-spacing: 3px;
    text-align: center;
  }

  /* Reduce hero spacing */
  .slider,
  .hero-section {
    padding: 0 15px;
  }

  /* ===== BUTTON ===== */

  .book-btn,
  .btn {
    padding: 10px 24px;
    font-size: 14px;
    border-radius: 30px;
  }

  /* ===== WHATSAPP FLOAT ===== */

  .whatsapp-float {
    width: 50px;
    height: 50px;
    bottom: 20px;
    right: 20px;
  }

  .whatsapp-float i {
    font-size: 20px;
  }

  /* ===== MOBILE MENU OVERLAY ===== */

  .site-mobile-menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;

    backdrop-filter: blur(18px);
    background: rgba(10, 10, 10, 0.95);

    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 9999;
  }

  .site-mobile-menu ul {
    list-style: none;
    padding: 0;
    text-align: center;
  }

  .site-mobile-menu li {
    margin: 22px 0;
  }

  .site-mobile-menu a {
    font-size: 20px;
    letter-spacing: 2px;
    color: #fff;
    text-decoration: none;
    transition: 0.3s ease;
  }

  .site-mobile-menu a:hover {
    color: #c5a47e;
  }

}
/* =============================== */
/* ===== FIX MOBILE NAV GRID ===== */
/* =============================== */

@media (max-width: 991px) {

  /* Hide desktop nav completely */
  .site-menu {
    display: none !important;
  }

  /* Make mobile menu full screen */
  .site-mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    background: rgba(10, 10, 10, 0.95);
    backdrop-filter: blur(12px);

    display: flex !important;
    justify-content: center;
    align-items: center;

    z-index: 99999;
  }

  /* FORCE vertical layout */
  .site-mobile-menu ul {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
    gap: 25px;
    padding: 0;
    margin: 0;
  }

  .site-mobile-menu li {
    list-style: none;
  }

  .site-mobile-menu a {
    font-size: 20px;
    letter-spacing: 3px;
    text-decoration: none;
    color: white;
    transition: 0.3s ease;
  }

  .site-mobile-menu a:hover {
    color: #c5a47e;
  }

}
/* ================================= */
/* ===== FORCE CLEAN MOBILE NAV ==== */
/* ================================= */

@media (max-width: 991px) {

  /* Remove bootstrap column behavior */
  .site-navbar .row {
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
  }

  .site-navbar .col-3,
  .site-navbar .col-9,
  .site-navbar .col-2,
  .site-navbar .col-10 {
    flex: unset !important;
    max-width: unset !important;
    width: auto !important;
  }

  /* Hide desktop menu completely */
  .site-menu {
    display: none !important;
  }

  /* Show only logo + hamburger */
  .site-logo {
    font-size: 18px !important;
  }

  .site-menu-toggle {
    display: block !important;
    font-size: 26px;
    cursor: pointer;
    color: white;
  }

}
/* ============================= */
/* ===== FINAL MOBILE NAV ====== */
/* ============================= */

.mobile-toggle {
  display: none;
  font-size: 26px;
  cursor: pointer;
  z-index: 10000;
}

/* Desktop */
@media (min-width: 992px) {
  .mobile-toggle {
    display: none !important;
  }
}

/* Mobile */
@media (max-width: 991px) {

  /* Hide desktop nav */
  .site-menu {
    display: none !important;
  }

  /* Show hamburger */
  .mobile-toggle {
    display: block !important;
    color: #000; /* change to white if navbar dark */
  }

  /* Force navbar layout */
  .site-navbar .row {
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
  }

  .site-navbar .col-3,
  .site-navbar .col-9 {
    width: auto !important;
    max-width: none !important;
  }

}
/* ========================= */
/* ===== NAV RESPONSIVE ==== */
/* ========================= */

.mobile-toggle {
  display: none;
}

.desktop-menu {
  display: block;
}

@media (max-width: 991px) {

  .desktop-menu {
    display: none !important;
  }

  .mobile-toggle {
    display: block !important;
  }

  .mobile-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(10, 10, 10, 0.95);
    backdrop-filter: blur(12px);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999;
  }

  .mobile-overlay ul {
    list-style: none;
    padding: 0;
    text-align: center;
  }

  .mobile-overlay li {
    margin: 20px 0;
  }

  .mobile-overlay button {
    background: none;
    border: none;
    color: white;
    font-size: 20px;
    letter-spacing: 2px;
    cursor: pointer;
  }

}